@import url(https://unpkg.com/leaflet@1.5.1/dist/leaflet.css);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*{
  margin:0;
  padding: 0;
}
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#dbdbdb;
  font-size:16px;
}

ul {
  list-style: none;
}

a { text-decoration: none!important;}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input, button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  /* background: none; */
  /* border: none; */
  outline: none;
}
.App{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #636363;

}
form{
  display: block;
  position: relative;

}

form::after{
  content: '';
  display: block;
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: 1;
  background-image: linear-gradient(to bottom right,#3282a7,#5448fe);
}

form .form-header{
  position: relative;
  display: block;
  background-color: rgb(255, 255, 255);
  padding: 30px;
  z-index: 2;
}
form .form-header h2{
  color: #5448fe;
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 30px;
}

form .form-header .form-group{
  display: block;
  width: 300px;
  margin-bottom: 15px;
}

.form-header .form-group label{
  display: block;
  color: #333333;
  font-size: 12px;
  margin-bottom: 5px;
  transition: 0.4s;
  text-align: left;

}
.form-header .form-group:focus-within label{
  color:#5448fe;


}

form .form-header .form-group input {
  display: block;
  width: 100%;
  padding: 10px 15px;
  background-color: rgb(255, 250, 244);
  border-radius: 8px;
  transition: 0.4s;

}
form .form-header .form-group input:focus{
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
} 

form .form-header input[type="submit"]{
  display: inline-block;
  padding: 10px 15px;
  border-radius: 8px;
  background-image: linear-gradient(to left,#5448fe 50%, #3282a7 50%);
  background-size: 200%;
  background-position: 0%;
  transition: 0.4s;
  color: rgb(255, 251, 246);
  font-weight: 700;
  cursor: pointer;
}
form .form-header input[type="submit"]:hover{
  background-position:  100% 0%;
}


.logged{
  width: 100%;
  height: 100vh;
  
}



.titolo{
  color: #000000;
  
}

.scopri{
  border: 1px solid;
  border-radius: 10%;
}

.background{
  width: 100%;
  height: 100vh;
  
  
}

.topbar {
  text-align:right;
  position:absolute;
  width:100%;
